.avatar {
  display: flex;
  align-items: flex-start;
  flex: 0 0 70px;
  height: 70px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 15px;

  img {
    pointer-events: none;
  }
}

/* Large media */
@media (min-width: 992px) {
  .avatar {
    flex: 0 0 100px;
    height: 100px;
    margin-right: 20px;
  }
}
