@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
*,*::before,*::after{box-sizing:border-box}body,h1,h2,h3,h4,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;font-family:'Lato', sans-serif;font-size:100%;line-height:1.5;color:#000;scroll-behavior:smooth;text-rendering:optimizeSpeed}ul[class],ol[class]{list-style:none;padding:0}a:not([class]){color:#2196f3;-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto;text-decoration:none}img{max-width:100%;display:block}input,button,textarea,select{font:inherit}h1{font-size:1.5rem}h2{font-size:1.25rem}h3{font-size:1.125rem;margin-bottom:15px}h4{font-size:1rem;margin-bottom:15px}article{margin-bottom:34px}p{margin:0 0 .5rem}small{font-size:.75rem}@media (prefers-reduced-motion: reduce){*{-webkit-animation-duration:0.01ms !important;animation-duration:0.01ms !important;-webkit-animation-iteration-count:1 !important;animation-iteration-count:1 !important;transition-duration:0.01ms !important;scroll-behavior:auto !important}}.container{padding-left:15px;padding-right:15px;margin-left:auto;margin-right:auto;width:100%}.row{display:flex;flex-wrap:wrap;margin-left:-15px;margin-right:-15px}.col-md-6,.col-md-5{width:100%;padding-left:15px;padding-right:15px}.history-list li,.courses-list li,.details-list li,.skill-list li{display:flex;flex-direction:column;align-items:flex-start;margin-bottom:1rem}.article-title{display:flex;align-items:center}.date{display:block;font-weight:500;color:#a5a5a5;margin-bottom:.25rem}.skill-name{display:block;margin-bottom:5px}.portfolio-links{display:flex;flex-direction:column;align-items:flex-start}@media (min-width: 576px){.container{max-width:540px}}@media (min-width: 768px){.container{max-width:720px}.col-md-6{flex:0 0 50%;max-width:50%}}@media (min-width: 992px){body{font-size:100%}h1{font-size:2rem}h2{font-size:1.75rem}h3{font-size:1.5rem}h4{font-size:1.25rem}small{font-size:.875rem}header{padding:20px 0}.container{max-width:960px}.col-lg-5{flex:0 0 41.66667%;max-width:41.66667%}.offset-lg-1{margin-left:8.33333%}.article-title{background-position:0 6px}}@media (min-width: 1200px){.container{max-width:1140px}}@media (hover: hover){a:not([class]):hover{text-decoration:underline}}

header{position:-webkit-sticky;position:sticky;top:0;left:0;background-color:#fff;border-bottom:1px solid #e0e0e0;padding:15px 0;z-index:100}header .container{display:flex;align-items:center}

.avatar{display:flex;align-items:flex-start;flex:0 0 70px;height:70px;border-radius:50%;overflow:hidden;margin-right:15px}.avatar img{pointer-events:none}@media (min-width: 992px){.avatar{flex:0 0 100px;height:100px;margin-right:20px}}

.main{padding-top:40px}

.icon-box{display:flex;flex:0 1;margin-right:10px}











