@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap');

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  font-family: 'Lato', sans-serif;;
  font-size: 100%;
  line-height: 1.5;
  color: #000;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
}

/* Remove list styles on ul, ol elements with a class attribute and remove default padding */
ul[class],
ol[class] {
  list-style: none;
  padding: 0;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  color: #2196f3;
  text-decoration-skip-ink: auto;
  text-decoration: none;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.25rem;
}

h3 {
  font-size: 1.125rem;
  margin-bottom: 15px;
}

h4 {
  font-size: 1rem;
  margin-bottom: 15px;
}

article {
  margin-bottom: 34px;
}

p {
  margin: 0 0 .5rem;
}

small {
  font-size: .75rem;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* Helper classes */
.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.col-md-6,
.col-md-5 {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

.history-list,
.courses-list,
.details-list,
.skill-list {

  li {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 1rem;
  }
}

/* Title with icons */
.article-title {
  display: flex;
  align-items: center;
}

.date {
  display: block;
  font-weight: 500;
  color: #a5a5a5;
  margin-bottom: .25rem;
}

.skill-name {
  display: block;
  margin-bottom: 5px;
}

/* Portfolio navigation */
.portfolio-links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

/* Extra large media */
/* Media small */
@media (min-width: 576px) {

  .container {
    max-width: 540px;
  }
}

/* Medium media */
@media (min-width: 768px) {

  .container {
    max-width: 720px;
  }

  .col-md-6 {
    flex: 0 0 50%;
    max-width: 50%;
  }
}

/* Large media */
@media (min-width: 992px) {

  body {
    font-size: 100%;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.75rem;
  }

  h3 {
    font-size: 1.5rem;
  }

  h4 {
    font-size: 1.25rem;
  }

  small {
    font-size: .875rem;
  }

  header {
    padding: 20px 0;
  }

  .container {
    max-width: 960px;
  }

  .col-lg-5 {
    flex: 0 0 41.66667%;
    max-width: 41.66667%;
  }

  .offset-lg-1 {
    margin-left: 8.33333%;
  }

  .article-title {
    background-position: 0 6px;
  }
}

@media (min-width: 1200px) {

  .container {
    max-width: 1140px;
  }
}

/* The hover CSS media feature can be used to test whether the user's primary input mechanism can hover over elements */
@media (hover: hover) {
  a:not([class]):hover {
    text-decoration: underline;
  }
}
